      </div>
      <Scrollama
        :debug="false"
        :offset="0.5"
        @step-enter="brainHandler"
        class="main__scrollama"
      >
        <div class="step-brain" style="opacity: 0"></div>
        <div class="step-brain" data-step-no="1">
          <div class="step-brain-text">
            <h3 class="phase-title">Phase 1:</h3>
            <h2 class="brain-title">The Normal Brain</h2>
            <div class="line-sep-cont"><div class="line-sep"></div></div>
            <p>
              Your body naturally produces endorphins, which is the body’s
              natural version of opioids, and this is the same reward circuit
              that is accessed after working out, falling in love, or eating a
              good meal. They relieve pain and a create a general feeling of
              wellbeing.
            </p>
          </div>
        </div>
        <div class="step-brain" data-step-no="2">
          <div class="step-brain-text">
            <h3 class="phase-title">Phase 2:</h3>
            <h2 class="brain-title">The Mesolimbic Reward System</h2>
            <div class="line-sep-cont"><div class="line-sep"></div></div>
            <p>
              When an opioid is first taken, it creates a tidal wave in the
              reward circuits of the brain. It floods your frontal lobe with
              dopamine and replaces pain receptors with euphoria- encouraging
              repeated use and making the user seek the same high as their first
              dose.
            </p>
          </div>
        </div>
        <div class="step-brain" data-step-no="3">
          <div class="step-brain-text">
            <h3 class="phase-title">Phase 3:</h3>
            <h2 class="brain-title">The Dependence to Feel Normal</h2>
            <div class="line-sep-cont"><div class="line-sep"></div></div>
            <p>
              The Locus Coeruleus (LC) is an area of the brain that is
              critically involved in the production of opioid dependence and
              withdrawal. At this phase, the mesolimbic reward system changes
              and dependence means that the user no longer feels pleasure in the
              activities they used to without opioids. The brain functions more
              or less normally when the drugs are present and abnormally when
              they are not. Opioid tolerance occurs because the brain cells that
              have opioid receptors on them gradually become less responsive to
              stimulation.
            </p>
          </div>
        </div>
        <div class="step-brain" data-step-no="4">
          <div class="step-brain-text">
            <h3 class="phase-title">Phase 4:</h3>
            <h2 class="brain-title">The Changed Set Point Model</h2>
            <div class="line-sep-cont"><div class="line-sep"></div></div>
            <p>
              Your baseline is altered, no longer producing natural opioids and
              depending entirely on the medication to regulate. This is what
              addiction looks like. Withdrawal symptoms become unbearable, known
              as 'dopesick'. Cortisol, the stress hormone, is increased, making
              stress a driver for many addicts.
            </p>
          </div>
        </div>
        <div class="step-brain" data-step-no="5">
          <div class="step-brain-text">
            <h3 class="phase-title">Phase 5:</h3>
            <h2 class="brain-title">
              The Normalizing Effects of MAT Medications
            </h2>
            <div class="line-sep-cont"><div class="line-sep"></div></div>
            <p>
              Medication Assisted Treatment, or MAT, causes dependence, but
              because of its steadier influence on opioid receptors, it
              alleviates cravings and compulsive drug use. It moderates the
              exaggerated cortisol stress response that increases the danger of
              relapse in stressful situations. MAT is highly effective in
              recovery, but given the complex biological, psychological, and
              social aspects of the disease, it is most effective when combined
              with appropriate psychosocial treatments.
            </p>
          </div>
        </div>
        <div class="step-brain invisible" data-step-no="6">
          <div class="step-brain-text"></div>
        </div>
      </Scrollama>
    </div>
  </div>
</template>

<script>
import "intersection-observer";
import Scrollama from "../../vue-scrollama/src/Scrollama.vue";
import * as d3 from "d3";

const brainFill1 = d3.selectAll(".cls-2");
const brainFill2 = d3.selectAll(".cls-3");

export default {
  name: "App",
  components: {
    Scrollama,
  },
  data() {
    return {
      currStep: null,
    };
  },
  methods: {
    brainHandler({ element, index, direction }) {
      if (direction === "down") element.classList.add("active");
      // console.log(index);
      if (index === 0) this.phase0();
      if (index === 1) this.phase0();
      if (index === 2) this.phase2();
      if (index === 3) this.phase3();
      if (index === 4) this.phase4();
      if (index === 5) this.phase5();
    },

    brainStart() {
      d3.select("#front-lobe").attr("opacity", 0);
      d3.select("#LC-arrows-3").attr("opacity", 0);
      d3.select("#LC-center").attr("opacity", 0);
      d3.select("#front-lobe-arrows-2").attr("opacity", 0);
      d3.select("#VTA-2").attr("opacity", 0);
      d3.select("#brain-base").attr("opacity", 0.85);
    },

    phase0() {
      d3.select("#front-lobe").attr("opacity", 0);
      d3.select("#LC-arrows-3").attr("opacity", 0);
      d3.select("#LC-center").attr("opacity", 0);
      d3.select("#VTA-2").attr("opacity", 0);
      d3.select("#front-lobe-arrows-2").attr("opacity", 0);
      d3.select("#brain-base")
        .transition()
        .ease(d3.easeLinear)
        .duration(2000)
        .attr("opacity", 0.85);
      brainFill1.style("fill", "#f6adcd");
    },

    phase2() {
      d3.select("#brain-base")
        .transition()
        .ease(d3.easeLinear)
        .duration(2000)
        .attr("opacity", 0.4);
      d3.select("#front-lobe").attr("opacity", 0.5);
      d3.select("#LC-arrows-3").attr("opacity", 0);
      d3.select("#VTA-2").attr("opacity", 0.4);
      d3.select("#front-lobe-arrows-2").attr("opacity", 1);
      const arrowLineDrawn = d3.selectAll(".cls-5").attr("opacity", 1);
      const arrowDrawn = d3.selectAll(".cls-6").attr("opacity", 0);
      arrowLineDrawn
        .attr("stroke-dasharray", function () {
          return this.getTotalLength();
        })
        .attr("stroke-dashoffset", function () {
          return this.getTotalLength();
        })
        // reveal the paths
        .transition()
        .duration(4000)
        .attr("stroke-dashoffset", 0);
      arrowDrawn
        // reveal the arrowheads
        .transition()
        .duration(6000)
        .attr("opacity", 1)
        .attr("transform", function () {
          return "translate(" + this.x + "," + this.y + ")";
        });

      d3.select("#LC-center").attr("opacity", 1);
    },

    phase3() {
      d3.select("#brain-base")
        .transition()
        .ease(d3.easeLinear)
        .duration(2000)
        .attr("opacity", 1);
      brainFill2
        .transition()
        .ease(d3.easeLinear)
        .duration(4000)
        .style("fill", "#f17fb1")
        .attr("opacity", 1);
      brainFill1
        .transition()
        .ease(d3.easeLinear)
        .duration(4000)
        .style("fill", "#f17fb1")
        .attr("opacity", 1);
      d3.select("#front-lobe").attr("opacity", 0);
      d3.select("#LC-arrows-3").attr("opacity", 1);
      d3.select("#VTA-2").attr("opacity", 0);
      d3.select("#front-lobe-arrows-2").attr("opacity", 0);
      const arrowLineDrawn = d3.selectAll(".cls-5").attr("opacity", 1);
      const arrowDrawn = d3.selectAll(".cls-6").attr("opacity", 0);
      arrowLineDrawn
        .attr("stroke-dasharray", function () {
          return this.getTotalLength();
        })
        .attr("stroke-dashoffset", function () {
          return this.getTotalLength();
        })
        // reveal the paths
        .transition()
        .duration(4000)
        .attr("stroke-dashoffset", 0);
      arrowDrawn
        // reveal the arrowheads
        .transition()
        .duration(6000)
        .attr("opacity", 1)
        .attr("transform", function () {
          return "translate(" + this.x + "," + this.y + ")";
        });
    },
    phase4() {
      const arrowLineDrawn = d3.selectAll(".cls-5").attr("opacity", 1);
      const arrowDrawn = d3.selectAll(".cls-6");
      arrowLineDrawn
        .transition()
        .delay(0)
        .on("start", function repeat() {
          d3.active(this)
            .duration(1000)
            .attr("stroke-dasharray", function () {
              return this.getTotalLength();
            })
            .attr("stroke-dashoffset", function () {
              return this.getTotalLength();
            })
            .transition()
            .delay(200)
            .duration(1000)
            .attr("stroke-dasharray", function () {
              return this.getTotalLength();
            })
            .attr("stroke-dashoffset", function () {
              return this.getTotalLength();
            })
            // reveal the paths
            .transition()
            .duration(1000)
            .attr("stroke-dashoffset", 0)
            .transition()
            .delay(200)
            .duration(1000)
            .attr("stroke-dasharray", function () {
              return this.getTotalLength();
            })
            .attr("stroke-dashoffset", function () {
              return this.getTotalLength();
            })
            .transition()
            .delay(200)
            .duration(1000)
            .attr("stroke-dasharray", function () {
              return this.getTotalLength();
            })
            .attr("stroke-dashoffset", function () {
              return this.getTotalLength();
            })
            // reveal the paths
            .transition()
            .duration(2000)
            .attr("stroke-dashoffset", 0)
            .on("start", repeat);
        });

      arrowDrawn
        .attr("transform", function () {
          return "translate(" + this.x + "," + this.y + ")";
        })
        .transition()
        .delay(350)
        .on("start", function repeat() {
          d3.active(this)
            .attr("opacity", 0)
            .transition()
            .delay(350)
            .duration(2500)
            .attr("opacity", 1)
            .transition()
            .delay(350)
            .duration(2500)
            .attr("opacity", 0)
            .transition()
            .delay(350)
            .duration(2500)
            .attr("opacity", 1)
            .transition()
            .delay(350)
            .on("start", repeat);
        });

      d3.select("#brain-base")
        .transition()
        .delay(350)
        .on("start", function repeat() {
          d3.active(this)
            .attr("opacity", 0.5)
            .transition()
            .delay(350)
            .duration(2500)
            .attr("opacity", 1)
            .transition()
            .delay(350)
            .duration(2500)
            .attr("opacity", 0.5)
            .transition()
            .delay(350)
            .duration(2500)
            .attr("opacity", 1)
            .transition()
            .delay(350)
            .on("start", repeat);
        });

      d3.select("#front-lobe").attr("opacity", 0);
      d3.select("#LC-arrows-3").attr("opacity", 1);
      d3.select("#VTA-2").attr("opacity", 0);
      d3.select("#front-lobe-arrows-2").attr("opacity", 0);

      //animate arrows
    },

    phase5() {
      d3.select("#brain-base")
        .transition()
        .ease(d3.easeLinear)
        .duration(2000)
        .attr("opacity", 0.7);

      //animate arrows
      const arrowLineDrawn = d3.selectAll(".cls-5").attr("opacity", 1);
      const arrowDrawn = d3.selectAll(".cls-6");
      arrowLineDrawn
        .transition()
        .duration(4000)
        .attr("stroke-dasharray", function () {
          return this.getTotalLength();
        })
        .attr("stroke-dashoffset", function () {
          return this.getTotalLength();
        });
      arrowDrawn
        .transition()
        .duration(4000)
        .attr("transform", "translate(0,200)")
        .attr("opacity", 0);
    },
  },
  mounted() {
    this.brainStart();
  },
};
</script>

